  const questionId = question.id

  if (questionId.startsWith('A')) {
    return 'æ•´ä½“ç–²æƒ«å¼ºåº¦'
  } else if (questionId.startsWith('B')) {
    if (['B1', 'B2', 'B3', 'B4'].includes(questionId)) return 'å·¥ä½œ/å­¦ä¹ '
    if (['B5', 'B6', 'B7', 'B8'].includes(questionId)) return 'äººé™…/å®¶åº­'
    if (['B9', 'B10', 'B11', 'B12'].includes(questionId)) return 'è‡ªæˆ‘è¦æ±‚'
  } else if (questionId.startsWith('C')) {
    if (['C1', 'C2', 'C3', 'C4'].includes(questionId)) return 'æƒ…ç»ªè¿‡è½½å‹'
    if (['C5', 'C6', 'C7', 'C8'].includes(questionId)) return 'è´£ä»»ç»‘æ¶å‹'
    if (['C9', 'C10', 'C11', 'C12'].includes(questionId)) return 'å¯¹æ¯”ç„¦è™‘å‹'
    if (['C13', 'C14', 'C15', 'C16'].includes(questionId)) return 'é«˜å‹ç¨³å®šå‹'
  } else if (questionId.startsWith('D')) {
    return 'æ¢å¤èµ„æº'
  }

  return ''
}

// ä¿å­˜ç­”æ¡ˆ
