  // å¯ä»¥åœ¨è¿™é‡Œæ·»åŠ å…¶ä»–é€»è¾‘ï¼Œæ¯”å¦‚è‡ªåŠ¨è·³è½¬ç­‰
  console.log(`Question ${questionId} answered with score ${score}`)
