import { defineStore } from 'pinia';
import type { AppState } from '@/types';
import { calculateFatigueResult } from '@/utils/calculator';
import { getAllQuestionIds, getTotalQuestionCount } from '@/data/questions';

export const useFatigueTestStore = defineStore('fatigueTest', {
  state: (): AppState & {
    startTime: number | null;
    endTime: number | null;
  } => ({
    currentStep: 'introduction',
    answers: {} as Record<string, number>,
    result: null,
    isCalculating: false,
    currentQuestionIndex: 0,
    startTime: null,
    endTime: null
  }),

  getters: {
    /**
     * è·å–å½“å‰è¿›åº¦
     */
    progress: (state): number => {
      const totalQuestions = getTotalQuestionCount();
      const answeredQuestions = Object.keys(state.answers).length;
      return Math.round((answeredQuestions / totalQuestions) * 100);
    },

    /**
     * è·å–å·²å®Œæˆé¢˜ç›®æ•°
     */
    answeredCount: (state): number => {
      return Object.keys(state.answers).length;
    },

    /**
     * è·å–æ€»é¢˜ç›®æ•°
     */
    totalQuestions: (): number => {
      return getTotalQuestionCount();
    },

    /**
     * æ£€æŸ¥æ˜¯å¦å¯ä»¥æäº¤ï¼ˆæ‰€æœ‰é¢˜ç›®éƒ½å·²å›ç­”ï¼‰
     */
    canSubmit: (state): boolean => {
      const allQuestionIds = getAllQuestionIds();
      return allQuestionIds.every(id => state.answers[id] !== undefined);
    },

    /**
     * è·å–æµ‹è¯•ç”¨æ—¶ï¼ˆç§’ï¼‰
     */
    duration: (state): number | null => {
      if (!state.startTime) return null;
      const endTime = state.endTime || Date.now();
      return Math.round((endTime - state.startTime) / 1000);
    }
  },

  actions: {
    /**
     * ä¿å­˜ç”¨æˆ·ç­”æ¡ˆ
     */
    saveAnswer(questionId: string, score: number) {
      this.answers[questionId] = score;

      // ä¿å­˜åˆ°localStorageï¼ˆå¯é€‰ï¼‰
      try {
        localStorage.setItem('fbi_answers', JSON.stringify(this.answers));
      } catch (error) {
        console.warn('æ— æ³•ä¿å­˜ç­”æ¡ˆåˆ°æœ¬åœ°å­˜å‚¨:', error);
      }
    },

    /**
     * å¼€å§‹æµ‹è¯•
     */
    startTest() {
      this.currentStep = 'questionnaire';
      this.startTime = Date.now();
      this.currentQuestionIndex = 0;

      // æ¸…ç©ºä¹‹å‰çš„ç­”æ¡ˆ
      this.answers = {};
      this.result = null;
      this.isCalculating = false;

      // æ¸…é™¤æœ¬åœ°å­˜å‚¨çš„ç­”æ¡ˆ
      try {
        localStorage.removeItem('fbi_answers');
      } catch (error) {
        console.warn('æ— æ³•æ¸…é™¤æœ¬åœ°å­˜å‚¨:', error);
      }
    },

    /**
     * è¿›å…¥ç»“æœé¡µé¢
     */
    goToResults() {
      this.currentStep = 'results';
    },

    /**
     * è¿”å›ä»‹ç»é¡µé¢
     */
    restart() {
      this.currentStep = 'introduction';
      this.answers = {};
      this.result = null;
      this.isCalculating = false;
      this.currentQuestionIndex = 0;
      this.startTime = null;
      this.endTime = null;

      // æ¸…é™¤æœ¬åœ°å­˜å‚¨
      try {
        localStorage.removeItem('fbi_answers');
      } catch (error) {
        console.warn('æ— æ³•æ¸…é™¤æœ¬åœ°å­˜å‚¨:', error);
      }
    },

    /**
     * è®¡ç®—æµ‹è¯„ç»“æœ
     */
    async calculateResults() {
      if (!this.canSubmit) {
        throw new Error('è¿˜æœ‰é¢˜ç›®æœªå®Œæˆ');
      }

      this.isCalculating = true;

      try {
        // æ¨¡æ‹Ÿè®¡ç®—å»¶è¿Ÿï¼Œè®©ç”¨æˆ·çœ‹åˆ°åŠ è½½çŠ¶æ€
        await new Promise(resolve => setTimeout(resolve, 1500));

        // è®¡ç®—ç»“æœ
        this.result = calculateFatigueResult(this.answers);
        this.endTime = Date.now();

        // è¿›å…¥ç»“æœé¡µé¢
        this.goToResults();

      } catch (error) {
        console.error('è®¡ç®—ç»“æœå¤±è´¥:', error);
        throw error;
      } finally {
        this.isCalculating = false;
      }
    },

    /**
     * ä»æœ¬åœ°å­˜å‚¨æ¢å¤ç­”æ¡ˆ
     */
    restoreAnswers() {
      try {
        const saved = localStorage.getItem('fbi_answers');
        if (saved) {
          const savedAnswers = JSON.parse(saved);
          this.answers = savedAnswers;
        }
      } catch (error) {
        console.warn('æ— æ³•æ¢å¤æœ¬åœ°å­˜å‚¨çš„ç­”æ¡ˆ:', error);
      }
    },

    /**
     * å¯¼å‡ºç­”æ¡ˆæ•°æ®ï¼ˆç”¨äºåˆ†ææˆ–å¤‡ä»½ï¼‰
     */
    exportAnswers(): string {
      const exportData = {
        answers: this.answers,
        timestamp: new Date().toISOString(),
        duration: this.duration,
        version: '1.0.0'
      };
      return JSON.stringify(exportData, null, 2);
    },

    /**
     * è·å–ç‰¹å®šæ¨¡å—çš„å®Œæˆæƒ…å†µ
     */
    getModuleProgress(module: string): {
      completed: number;
      total: number;
      percentage: number;
    } {
      const moduleQuestions: Record<string, string[]> = {
        'A': ['A1', 'A2', 'A3', 'A4', 'A5', 'A6', 'A7', 'A8'],
        'B': ['B1', 'B2', 'B3', 'B4', 'B5', 'B6', 'B7', 'B8', 'B9', 'B10', 'B11', 'B12'],
        'C': ['C1', 'C2', 'C3', 'C4', 'C5', 'C6', 'C7', 'C8', 'C9', 'C10', 'C11', 'C12', 'C13', 'C14', 'C15', 'C16'],
        'D': ['D1', 'D2', 'D3', 'D4']
      };

      const questions = moduleQuestions[module] || [];
      const completed = questions.filter(id => this.answers[id] !== undefined).length;
      const total = questions.length;
      const percentage = Math.round((completed / total) * 100);

      return { completed, total, percentage };
    },

    /**
     * è·³è½¬åˆ°æŒ‡å®šé¢˜ç›®
     */
    goToQuestion(index: number) {
      const totalQuestions = getTotalQuestionCount();
      if (index >= 0 && index < totalQuestions) {
        this.currentQuestionIndex = index;
      }
    },

    /**
     * ä¸‹ä¸€é¢˜
     */
    nextQuestion() {
      const totalQuestions = getTotalQuestionCount();
      if (this.currentQuestionIndex < totalQuestions - 1) {
        this.currentQuestionIndex++;
      }
    },

    /**
     * ä¸Šä¸€é¢˜
     */
    previousQuestion() {
      if (this.currentQuestionIndex > 0) {
        this.currentQuestionIndex--;
      }
    },

    /**
     * éªŒè¯å½“å‰ç­”æ¡ˆçš„å®Œæ•´æ€§
     */
    validateAnswers(): {
      isValid: boolean;
      missingQuestions: string[];
      warnings: string[];
    } {
      const allQuestionIds = getAllQuestionIds();
      const missingQuestions = allQuestionIds.filter(id => this.answers[id] === undefined);
      const warnings: string[] = [];

      // æ£€æŸ¥æ˜¯å¦æœ‰æç«¯ç­”æ¡ˆï¼ˆæ‰€æœ‰é¢˜ç›®éƒ½é€‰åŒä¸€ä¸ªé€‰é¡¹ï¼‰
      const answerValues = Object.values(this.answers);
      if (answerValues.length > 0) {
        const uniqueValues = new Set(answerValues);
        if (uniqueValues.size === 1) {
          warnings.push('æ‰€æœ‰é¢˜ç›®éƒ½é€‰æ‹©äº†ç›¸åŒçš„é€‰é¡¹ï¼Œå»ºè®®é‡æ–°å®¡è§†æ¯ä¸ªé¢˜ç›®');
        }
      }

      // æ£€æŸ¥æ¨¡å—å®Œæˆæƒ…å†µ
      const moduleProgress = {
        A: this.getModuleProgress('A'),
        B: this.getModuleProgress('B'),
        C: this.getModuleProgress('C'),
        D: this.getModuleProgress('D')
      };

      Object.entries(moduleProgress).forEach(([module, progress]) => {
        if (progress.completed < progress.total) {
          warnings.push(`${module}æ¨¡å—è¿˜æœ‰${progress.total - progress.completed}é“é¢˜ç›®æœªå®Œæˆ`);
        }
      });

      return {
        isValid: missingQuestions.length === 0,
        missingQuestions,
        warnings
      };
    }
  }
});